import "./styles.css";

import { Configuration, OpenAIApi } from "openai";
import { useState } from "react";
function App() {
  const [prompt, setPrompt] = useState("");
  const [result, setResult] = useState("");
  const [result2, setResult2] = useState("");
  const [result3, setResult3] = useState("");
  const [result4, setResult4] = useState("");

  const configuration = new Configuration({
    apiKey: "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"
  });
  const openai = new OpenAIApi(configuration);

  const generateImage = async () => {
    const response = await openai.createImage({
      prompt: prompt,
      n: 4,
      size: "1024x1024"
    });
    setResult(response.data.data[0].url);
    setResult2(response.data.data[1].url);
    setResult3(response.data.data[2].url);
    setResult4(response.data.data[3].url);
  };
  console.log(result2);
  return (
    <div className="App">
      <div className="app-main">
        <h3>Generate Image using OpenAi</h3>
        <input
          onChange={(e) => setPrompt(e.target.value)}
          className="app-input"
        />
        <button onClick={generateImage}>Click To Generate</button>

        {result.length > 0 ? (
          <img className="result-image" src={result} alt="img" />
        ) : (
          <></>
        )}
        {result2.length > 0 ? (
          <img className="result-image" src={result2} alt="img" />
        ) : (
          <></>
        )}
        {result3.length > 0 ? (
          <img className="result-image" src={result3} alt="img" />
        ) : (
          <></>
        )}
        {result4.length > 0 ? (
          <img className="result-image" src={result4} alt="img" />
        ) : (
          <></>
        )}
      </div>
    </div>
  );
}

export default App;
